// TYPES
const SET_NAME = "SET_NAME";
const SET_AGE = "SET_AGE";

//INITIAL STATE
const initialState = {
  name: "Els",
  age: 18,
};

//ACTION CREATORS
export const setName = (str) => ({
  type: SET_NAME,
  payload: str,
});
export const setAge = (nr) => ({
  type: SET_AGE,
  payload: nr,
});

//REDUCER
const personReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case SET_NAME:
      return { ...state, name: payload };
    case SET_AGE:
      return { ...state, age: payload };
    default:
      return state;
  }
};

export default personReducer;
