import store from "./redux/store";
import { setValue, increment, decrement } from "./redux/counter";
//of import * as actions from "./redux/counter";
import { add, remove } from "./redux/klasvrienden";
import { setAge, setName } from "./redux/person";
import { getMovies } from "./redux/movies";

/**
 * COUNTER ZONE
 */

// UITLEZEN VAN STOREDATA (.getState())
function updateCounterValue() {
  const { counter } = store.getState().counterState; //extracting child as var from object = ES6 object destructuring
  document.getElementById("counter").innerText = counter;
  document.getElementById("counterfield").value = counter;
}

updateCounterValue();

//ELKE KEER DAT DE STORE AANGEPAST WORDT
store.subscribe(updateCounterValue);

//UISTUREN VAN ACTION OM ZO ONRECHTSTREEKS DATA TE MANIPULEREN

document.getElementById("inc").onclick = () => store.dispatch(increment());

document.getElementById("dec").onclick = () => store.dispatch(decrement());

document.getElementById("counterfield").oninput = (e) =>
  store.dispatch(setValue(parseInt(e.target.value)));

/**
 * VRIENDEN ZONE
 */

function renderfriends() {
  document.getElementById("vrienden").innerHTML = store
    .getState()
    .vriendenState.vrienden.map(
      (vriendObj) =>
        `<li data-id="${vriendObj.id}">${vriendObj.name} (${vriendObj.id})</li>`
    )
    .join("");
}

renderfriends();

store.subscribe(renderfriends);

document.getElementById("friendForm").onsubmit = (e) => {
  e.preventDefault();
  store.dispatch(add(document.querySelector("#friendForm input").value));
  document.querySelector("#friendForm input").value = "";
};

document.getElementById("vrienden").onclick = (e) => {
  if (e.target.nodeName === "LI") {
    store.dispatch(remove(e.target.dataset.id));
  }
};

/**
 * PERSONZONE
 */

function renderPerson() {
  const { age, name } = store.getState().personState;
  document.getElementById("age").innerText = age;
  document.getElementById("name").innerText = name;
  document.getElementById("namefield").value = name;
  document.getElementById("agefield").value = age;
}
renderPerson();

store.subscribe(renderPerson);

document.getElementById("namefield").oninput = (e) =>
  store.dispatch(setName(e.target.value));

document.getElementById("agefield").oninput = (e) =>
  store.dispatch(setAge(parseInt(e.target.value)));

/**
 * MOVIEZONE
 */

document.getElementById("movieform").onsubmit = (e) => {
  e.preventDefault();
  store.dispatch(getMovies(document.querySelector("#movieform input").value));
  document.querySelector("#movieform input").value = "";
};

function movieRender() {
  const { movie, loading, movies } = store.getState().movieState;
  document.getElementById("titel").innerText = movie;
  if (loading) {
    document.getElementById("loading").style.display = "block";
  } else {
    document.getElementById("loading").style.display = "none";
  }
  if (movies) {
    document.getElementById("moviegrid").innerHTML = movies
      .map((movie) => `<li>${movie.title}</li>`)
      .join("");
  } else {
    document.getElementById("moviegrid").style.display = "none";
  }
}

movieRender();

store.subscribe(movieRender);
