import { v4 as uuid } from "uuid";
// TYPES
const ADD = "ADD";
const REMOVE = "REMOVE";

//INITIAL STATE
const initialState = {
  vrienden: [
    {
      id: uuid(),
      name: "Piet",
    },
    {
      id: uuid(),
      name: "Cindy",
    },
    {
      id: uuid(),
      name: "Jos",
    },
  ],
};

//ACTION CREATORS
export const add = (str) => ({
  type: ADD,
  payload: str,
});

export const remove = (id) => ({
  type: REMOVE,
  payload: id,
});

//REDUCER
const counterReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case ADD:
      return {
        ...state,
        vrienden: [
          ...state.vrienden,
          {
            id: uuid(),
            name: payload,
          },
        ],
      };
    case REMOVE:
      return {
        ...state,
        vrienden: state.vrienden.filter((vriend) => vriend.id != payload),
      };
    default:
      return state;
  }
};

export default counterReducer;
