import axios from "axios";

// INITIALSTATE
const initialState = {
  movies: [],
  loading: false,
  movie: "",
  error: false,
};

// TYPES
const MOVIE_FETCH_START = "MOVIE_FETCH_START";
const MOVIE_FETCH_SUCCESS = "MOVIE_FETCH_SUCCESS";
const MOVIE_FETCH_FAIL = "MOVIE_FETCH_FAIL";

// ACTIONCREATORS

const movieFetchStart = (str) => ({
  type: MOVIE_FETCH_START,
  payload: str,
});

const movieFetchFail = () => ({
  type: MOVIE_FETCH_FAIL,
});

const movieFetchSuccess = (movies) => ({
  type: MOVIE_FETCH_SUCCESS,
  payload: movies,
});

export const getMovies = (str) => async (dispatch, getState) => {
  //movie saven en loading true
  dispatch(movieFetchStart(str));
  try {
    const response = await axios(
      `https://api.themoviedb.org/3/search/movie?api_key=10fa74251cfff94026cb589d95b3db91&language=en-US&query=${
        getState().movieState.movie
      }&page=1&include_adult=false`
    );
    dispatch(movieFetchSuccess(response.data.results));
  } catch (error) {
    dispatch(moveFetchFail());
  }
};

// REDUCER

const movieReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case MOVIE_FETCH_START:
      return { ...state, movie: payload, loading: true, error: false };
    case MOVIE_FETCH_SUCCESS:
      return { ...state, loading: false, error: false, movies: payload };
    case MOVIE_FETCH_FAIL:
      return { ...state, loading: false, error: true };
    default:
      return state;
  }
};

export default movieReducer;
